import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Servico } from '../../../modelo/Servico'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'servico-dados-details',   templateUrl: 'servico_dadosDetails.component.html' }) export class ServicoDadosDetailsComponent implements OnInit {    servico: Servico;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.servico.Servico";                 let id = +params['id'];         this.servico = new Servico();         this.servico.cd_servico = id;         o.objetoJson = this.servico;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a servico. '; }      this.servico = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


